<template>
   <el-card class="card" v-loading="loading">
       <!-- 面包屑 -->
       <slot-common slot="header" style="font-size:18px;">
           <template slot="title">个人信息</template>
       </slot-common>
        <el-form ref="myform" class="account-form" :model="formData" :rules="rules" label-width="120px">
            <el-form-item label="用户名称" prop="name">
                <el-input v-model="formData.name" class="input"></el-input>
            </el-form-item>
            <el-form-item label="用户简介" prop="intro">
                <el-input v-model="formData.intro" class="input"></el-input>
            </el-form-item>
            <el-form-item label="用户邮箱" prop="email">
                <el-input v-model="formData.email" class="input"></el-input>
            </el-form-item>
            <el-form-item label="用户手机号">
                <el-input v-model="formData.mobile" disabled style="width:400px;"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="verifyUser" type="primary">保存信息</el-button>
            </el-form-item>
        </el-form>
        <!-- :show-file-list 用来设置显示上传文件列表  action 上传地址 -->
         <el-upload class="upload" action="" :show-file-list="false" :http-request="upImage">
        <img :src="formData.photo ? formData.photo : imgUrl"/>
      </el-upload>
   </el-card>
</template>

<script>
import evenBus from '../../utils/evenBus.js'
export default {
  data () {
    return {
      // 本地图片转化成base64位格式
      imgUrl: require('../../assets/img/404.png'),
      loading: false,
      formData: {
        name: '', // 用户名
        intro: '', // 头条号简介
        photo: '', // 用户头像
        email: '', // 邮箱
        mobile: '' // 手机号
      },
      rules: {
        name: [{ required: true, message: '用户名不能为空' },
          { min: 1, max: 7, message: '用户名称必须控制在1到7个字符' }],
        intro: [{
          required: true, message: '用户简介不能为空'
        }],
        email: [{ required: true, message: '邮箱不能为空' },
          { pattern: /^\w+((.\w+)|(-\w+))@[A-Za-z0-9]+((.|-)[A-Za-z0-9]+).[A-Za-z0-9]+$/, message: '邮箱格式不正确' }]
      }
    }
  },
  methods: {
    async upImage (params) {
      // console.log(params.file)
      this.loading = true // 上传图片出现加载效果
      let data = new FormData()
      data.append('photo', params.file)
      let result = await this.$http({
        url: '/user/photo',
        method: 'patch',
        data // 参数
      })
      // console.log(result.data.photo)
      this.formData.photo = result.data.photo
      evenBus.$emit('updateUserInfoSuccess') // 触发一个事件 更新用户头像成功了
      this.loading = false // 上传完毕关闭加载效果
    },
    // 手动校验
    verifyUser () {
      this.$refs.myform.validate(async (isOk) => {
        if (isOk) {
          await this.$http({
            url: '/user/profile',
            method: 'patch',
            data: this.formData
          })
          this.$message({ message: '保存成功', type: '提示' })
        }
      })
    },
    //  获取用户信息
    async getUserInfo () {
      let result = await this.$http({
        url: '/user/profile'
      })
      // console.log(result)
      this.formData = result.data
    }
  },
  created () {
    this.getUserInfo()
  }
}
</script>

<style lang="less" scoped>
.card{
    position: relative;
  .account-form {
    margin-left: 70px;
    .input {
        width: 400px;
    }
  }
  .upload{
      position: absolute;
      left: 700px;
      top: 80px;
      width: 230px;
      height: 230px;
      text-align: center;
      vertical-align: middle;
      img{
        width: 230px;
        height: 230px;
        border-radius: 50%;
      }
    }
}
</style>
